.preloader {
  z-index: 1001;
  cursor: pointer;
  background: rgba(146, 114, 149, .455);
  border: none;
  outline: none;
  padding: 1.25rem;
  transition: all .2s ease-in-out;
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  box-shadow: -5px -5px 20px #fff, 5px 5px 20px #babecc;
}

.loaded_hiding .preloader {
  opacity: 0;
  transition: opacity .3s;
}

.loaded .preloader {
  display: none;
}

.loading .preloader {
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader {
  width: 3.375rem;
  height: 3.375rem;
  border-top: .25rem solid #fff;
  border-right: .25rem solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

